const MyToken = ({tokens}) => {

    const copyToClipboard = (address) => {
        navigator.clipboard.writeText(address);
    };

    const openInNewTab = (address) => {
        window.open(`https://example.com/address/${address}`, '_blank');
    };


    useEffect(()=>{
        console.log("shooli", tokens.length)
    }, [tokens.length])
    return (
        <div className='flex flex-row'>
        {
            Array(tokens.length).fill().map((_, i)=>
            (<div key={i} className="isolate aspect-video w-96 rounded-xl bg-purple/30 shadow-lg ring-1 ring-black/5">
                <div className="isolate w-96 p-4 rounded-t-xl bg-purple/40 shadow-lg ring-1 ring-black/5">
                    <div className='flex gap-3'>
                        <h1 className='text-white font-bold'>{tokens[i].address.substring(0, 6)}...{tokens[i].address.substring(tokens[i].address.length - 4, tokens[i].address.length)}</h1>
    
                        <button className="font-bold" onClick={()=>copyToClipboard(tokens[i].address)}><FontAwesomeIcon icon={faCopy} className='hover:text-gray-400' /></button>
                        <button className="font-bold" onClick={()=>openInNewTab(tokens[i].address)}><FontAwesomeIcon icon={faExternalLinkAlt} className='hover:text-gray-400' /></button>
    
                    </div>
                    <div className='flex flex-col justify-between lg:flex-row'>
                        <div className='flex my-2 flex-col'>
                            <h1 className='text-2xl font-bold'>
                                {tokens[i].tokenSymbol}
                            </h1>
                            <h1 className='text-xl'>
                                Token Symbol
                            </h1>
                        </div>
                        <div className='flex my-2 flex-col'>
                            <h1 className='text-2xl font-bold'>
                                {Math.floor(parseFloat(ethers.utils.formatEther(tokens[i].totalSupply)))}
                            </h1>
                            <h1 className='text-xl'>
                                Total Supply
                            </h1>
                        </div>
                    </div>
                </div>
                <div className='p-4 flex flex-col'>
                    <div className='my-1'>
                        <h1 className='text-xl font-bold'>Token Name</h1>
                        <h1 className='border border-gray-500 rounded bg-slate-700 p-2 text-white font-semibold'>{tokens[i].tokenName}</h1>
                    </div>
                    <div className='my-1'>
                        <h1 className='text-xl font-bold'>Created by</h1>
                        <div className='flex justify-between border border-gray-500 rounded bg-slate-700 p-2 text-white font-semibold'>
                            <h1>{tokens[i].createdBy.substring(0, 6)}...{tokens[i].createdBy.substring(tokens[i].createdBy.length - 4, tokens[i].createdBy.length)}</h1>
                            <button className="font-bold" onClick={copyToClipboard}><FontAwesomeIcon icon={faCopy} className='hover:text-gray-400' /></button>
                        </div>
                    </div>
                    <div className='my-1'>
                        <h1 className='text-xl font-bold'>Holders</h1>
                        <h1 className='border border-gray-500 rounded bg-slate-700 p-2 text-white font-semibold'>{tokens[i].holders}</h1>
                    </div>
                    <div className='my-1'>
                        <h1 className='text-xl font-bold'>Balance</h1>
                        <h1 className='border border-gray-500 rounded bg-slate-700 p-2 text-white font-semibold'>{ethers.utils.formatEther(tokens[i].balance)}</h1>
                    </div>
                    <div className='my-1'>
                        <h1 className='text-xl font-bold'>Token Function</h1>
                        <h1 className='border border-gray-500 rounded bg-slate-700 p-2 text-white font-semibold'>{tokens[i].tokenFunction}</h1>
                    </div>
                </div>
            </div>)
            )

        }
        
        </div>
    );
};


const MyTokenList = () => {
    const [tokens, setTokens] = useState([]);
    const [gettingTokens, setGettingTokens] = useState(false)

    const MyTokenStyle = {
        backgroundImage: `url(${MyTokenBg})`,
    };

    let tx;
    let signer;
    let Name;

    const search = async () =>{
        setGettingTokens(true)
        let tokens_ = [];
        if (account === undefined) {
             alert('first connect your wallet');
             return setTokens([])
        }
        let readContract = new ethers.Contract(contract, contractAbi, provider)
        // let read = await readContract.generatedTokens(account[0]);
        // console.log(read)
        signer = provider.getSigner()
    
        try {
            let greeterContractWithSigner = readContract.connect(signer)
            tx = await greeterContractWithSigner.generatedTokens(account[0]);
        }catch(e) {
            console.log(e); 
        }
    
        for(let i =0; i < tx.length; i ++){
            let newContractAddress = tx[i];
            console.log(newContractAddress)
            let readcontractDetails = new ethers.Contract(newContractAddress, createdContractAbi, provider);
            let Symbol ;
            let TotalSupply;
            let balance;
             try {
                let signToContract = readcontractDetails.connect(signer);
                Name = await signToContract.name();
                Symbol = await signToContract.symbol();
                TotalSupply = await signToContract.totalSupply();
                balance = await signToContract.balanceOf(account[0])
                console.log(Name)
                console.log(Symbol)
             }catch (r){ 
                console.log(r)
             }
             tokens_.push({
                address: newContractAddress,
                tokenSymbol: Symbol,
                totalSupply: TotalSupply,
                tokenName: Name,
                createdBy: account[0],
                holders: 'BfrrrfG Token Holder',
                balance: balance,
                tokenFunction: 'No'  
            })
        }
        console.log(tokens_)
        setTokens([...tokens_])
        setGettingTokens(false)
    }

    return (
        <section
            className="bg-cover bg-center text-white font-font-1 mx-auto h-full"
            style={MyTokenStyle}
        >
            <div className="flex flex-col py-36 items-center justify-center h-full">
                <h1 className="text-4xl font-bold mb-12">My Token List</h1>
                <button onClick={search}>
                    {
                        gettingTokens? "Loading..." : "Visit your tokens"
                    }
                </button>
                <div className='flex flex-wrap px-20 gap-10 justify-between kosMadrDG'>
                    <MyToken tokens={tokens}></MyToken>
                </div>
            </div>
        </section>
    );
};




export default MyTokenList;
